import $ from "jquery";

export default {
    ApplyStyles: function() {
        $("h1").addClass("ms-font-su");
        $("h2").addClass("ms-font-xxl");
        $("h3").addClass("ms-font-xl");
        $("p").addClass("ms-font-m");
    }
};



